<template>
  <header class="header">
    <div class="header__wrap">
      <div class="header__left">
        <router-link to="/" class="header__link">
          <img class="header__logo" src="@/assets/bitcat-logo.png" alt="Logo">
        </router-link>
        <div id="google_translate_element" class="custom-translate"></div>
      </div>
      <nav class="header__nav">
        <button 
          class="universe-ai-button" 
          :class="{ 'ai-enabled': isAiEnabled }"
          @click="toggleAi"
        >
          <span class="button-text">AI</span>
          <span class="switch-track">
            <span class="switch-thumb">
              <i class="fas fa-robot"></i>
            </span>
          </span>
        </button>
        <div class="dropdown">
          <button class="dropdown-toggle" @click="toggleMenu">
            Universe
            <span class="arrow">â–¼</span>
          </button>
          <div class="dropdown-menu" :class="{ 'show-mobile': isOpen }">
            <router-link @click="isOpen = false" class="dropdown-item" to="/welcome">Welcome</router-link>
            <router-link @click="isOpen = false" class="dropdown-item" to="/ai-agents">AI Agents</router-link>
            <router-link @click="isOpen = false" class="dropdown-item" to="/solo">Solo Game</router-link>
            <router-link @click="isOpen = false" class="dropdown-item" to="/hide-seek">PvP Game</router-link>
            <router-link @click="isOpen = false" class="dropdown-item" to="/nft-mint">NFT Minting</router-link>
            <router-link @click="isOpen = false" class="dropdown-item" to="/dao">DAO Voting</router-link>
          </div>
        </div>
        <router-link v-if="!store.getters.isAuthenticated" class="header__nav-item-my-bitcat" to="/my"><strong>My BITCAT</strong></router-link>
        <router-link v-else class="header__nav-item-my-bitcat header__nav-item-my-bitcat-authenticated" to="/my">
          <span>{{ store.getters.userTag }}</span>
          <small v-if="store.getters.hasWallet">{{ shortWallet }}</small>
          <small v-else>not connected</small>
        </router-link>
      </nav>
    </div>
  </header>
</template>

<script setup>
import { ref, onMounted, defineEmits, computed } from 'vue';
import store from '@/vuex';

const shortWallet = computed(() => {
  return store.getters.walletAddress ? store.getters.walletAddress.slice(0, 4) + '...' + store.getters.walletAddress.slice(-4) : 'not connected';
});

const isOpen = ref(false);
const isAiEnabled = ref(true);

const emit = defineEmits(['toggleAi']);

onMounted(() => {
  const aiEnabled = localStorage.getItem('bitcat_ai_enabled');
  isAiEnabled.value = aiEnabled === null ? true : aiEnabled === 'true';
});

const toggleAi = () => {
  isAiEnabled.value = !isAiEnabled.value;
  emit('toggleAi', isAiEnabled.value);
};

const toggleMenu = () => {
  isOpen.value = !isOpen.value;
};
</script>

